:root {
  --primary-text-color: #131313;
  --title-text-color: #000;
  --title-text-color-secondary: #fcfcfc;
  --background-color: #84a178;
  --button-color: #f8d24e;
  --button-color-accent: #0a8817;
  --button-text-black: #000;
  --button-text-white: #fcfcfc;
  --transition-func: 250ms cubic-bezier(.4, 0, .2, 1);
  --indent: 30px;
  --items: 3;
  --text-color: #fff;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  background-color: var(--background-color);
  color: var(--primary-text-color);
  letter-spacing: .04em;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
}

h1, h2, h3, h4, h5, h6, p, ul {
  margin: 0;
}

img {
  max-width: 100%;
  display: block;
}

button {
  cursor: pointer;
}

.list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.link {
  color: inherit;
  text-decoration: none;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.button-color {
  background-color: var(--button-color);
  color: var(--button-text-black);
  transition: background-color var(--transition-func);
  border: none;
  border-radius: 10px;
}

.button-color:hover, .button-color:focus {
  background-color: var(--button-color-accent);
}

.button-transparent {
  border: 2px solid var(--button-color);
  color: var(--button-text-white);
  transition: border-color var(--transition-func);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
}

.button-transparent:focus, .button-transparent:hover {
  border-color: var(--button-color-accent);
}

.backdrop {
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

